<template>
  <div id="app">
		<div class="form">
      <div class="form-item" :key="item" v-for="(item, index) in dlInfos">
        <input type="text" class="title-input" placeholder='deeplink页面标题' v-model='item.title' />
        <input type="text" class="url-input" placeholder='deeplink路径' v-model="item.url" />
        <a href='javascript:void(0);' @click='handleAdd(index)'>+</a>
        <a v-if='dlInfos.length>1' href='javascript:void(0);' @click='handleDelete(index)'>-</a>
      </div>
		</div>
    <div class='javascript-editor' id="javascript-editor"></div>
    <textarea class="cp-console" v-model='result' style='display:none;'></textarea>
    <pre id='code' style='display:none;'>
(function(){
  var apiObj = window.EventJavascriptInterface;
  if(apiObj && apiObj.stopProgress){
    apiObj.stopProgress();
  }
  function getExt(url){
    var urlHost = url.split('?')[0];
    var ext = 'h5';
    if (urlHost.indexOf('meizu.com') != -1 || urlHost.indexOf('flyme.cn') != -1 || urlHost.indexOf('mzres.com') != -1) {
      ext = 'h5_ext';
    }
    return ext;
  }
  function gotoH5Page(url, title, type) {
    if(apiObj && apiObj.gotoPage) {
      apiObj.gotoPage(JSON.stringify({
        url: url,
        title: title,
        type: type
      }));
    } else {
      location.href = url;
    }
  }
  var btns = $('[data-url="javascript:void(0);"]');
  btns.forEach(function(btn, index){
    $(btn).click(function(){
      var one = nav_links[index];
      if(!one){
        alert('链接缺失');
        return;
      }
      gotoH5Page(one.url, one.title, getExt(one.url));
    });
  });
  var nav_links = [];
})();
    </pre>
  </div>
</template>

<script>
import * as ace from 'brace';
import 'brace/mode/javascript';
import 'brace/theme/monokai';

var editor = null;
var code = '';

export default {
  name: 'app',
  data () {
    return {
      dlInfos: [
        {
          title: '',
          url: ''
        }
      ]
    }
  },
  computed: {
    result(){
      var r = this.dlInfos.map(function(item){
        return item.title + '-' + item.url;
      });
      editor && editor.setValue(this.getCode());
      return r;
    }
  },
  methods: {
    getCode(){
      return code.replace(/var nav_links = \[\];/m, `var nav_links = ${JSON.stringify(this.dlInfos)};`);
    },
    handleTitleInput(index, value){
      Vue.set(this.dlInfos, index, {
        title: value,
        url: 1
      });
    },
    handleAdd(index){
      this.dlInfos.splice(index+1, 0, {
        title: '',
        url: ''
      });
    },
    handleDelete(index){
      this.dlInfos.splice(index, 1);
    }
  },
  mounted() {
    editor = ace.edit('javascript-editor');
    editor.getSession().setMode('ace/mode/javascript');
    editor.setTheme('ace/theme/monokai');
    code = document.getElementById('code').innerText;
  }
}
</script>

<style>
input,select,textarea {
  outline: none;
}
input{
  box-sizing: border-box;
  font-size: 14px;
  padding: 2px;
}
.title-input{
  flex: 1;
  margin-right: 20px;
}
.url-input{
  flex: 3;
}
.form-item{
  display: flex;
  line-height: 24px;
}
a {
  display: inline-block;
  cursor: pointer;
  width: 20px;
  text-align: center;
  color: #0baaf7;
  text-decoration: none;
}
.form-item{
  margin-bottom: 10px;
}
.form-item:last-child{
  margin-bottom: 30px;
}
#app {
  margin: 10px auto;
  width: 960px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 18px;
}
.javascript-editor {
  height: 500px;
}

</style>



